let access_token = '', upload_url1 = '', upload_url2 = '';

const blueBackground = {
	backgroundColor: 'var(--accent)'
};

const redBackground = {
	backgroundColor: 'var(--dynamic_red)'
};

class App extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			initialized: false,
			popout: <ScreenSpinner/>,
			activeStory: 'tab_main',
			slideTabIndex: 0,
			activePanelShop: "main",
			historyShop: ["main"],
			activePanelOther: "profile",
			historyOther: ["profile"],
			createStickerContextOpened: false,
			balance_manipulation: false,
			stickers: [ { title: "Бисквит", author: "Ирина Брэдкэт", preview: "https://vk.com/sticker/2-533-cover-140b-0", price: 1, description: "Хвост торчком, большие ушки —\nБерегитесь, печенюшки!", stickers: [ 'https://vk.com/sticker/1-18945-64', 'https://vk.com/sticker/1-18946-64', 'https://vk.com/sticker/1-18947-64', 'https://vk.com/sticker/1-18948-64', 'https://vk.com/sticker/1-18949-64', 'https://vk.com/sticker/1-18950-64', 'https://vk.com/sticker/1-18951-64', 'https://vk.com/sticker/1-18952-64' ] }, { title: "Илай", author: "Ирина Брэдкэт", preview: "https://vk.com/sticker/1-18781-128", price: 1, description: "Вышел мишка из берлоги — поселился в диалоге.", stickers: [ 'https://vk.com/sticker/1-18781-128', 'https://vk.com/sticker/1-18782-128', 'https://vk.com/sticker/1-18783-128', 'https://vk.com/sticker/1-18784-128', 'https://vk.com/sticker/1-18785-128', 'https://vk.com/sticker/1-18786-128', 'https://vk.com/sticker/1-18787-128', 'https://vk.com/sticker/1-18788-128' ] } ],
			stickers_panel: null,
			sticker_info: { title: "", author: "", preview: "", price: 0, description: "", stickers: [] },
			user: {
				first_name: '',
				last_name: '',
				photo_100: '',
				stickers:[ ],
				balance: 0,
				stickers_history: [ ]
			},
			create_panel: { title: "", preview: "", price: 1, description: "", stickers: [] },
			captcha : { text: '', url: '', done: false }
		};

		this.onStoryChange = this.onStoryChange.bind(this);
		this.onChange = this.onChange.bind(this);
		this.uploadFile = this.uploadFile.bind(this);
		this.toggleCreateStickerContext = this.toggleCreateStickerContext.bind(this);
		this.sync = this.sync.bind(this);
		this.install = this.install.bind(this);
		this.delete = this.delete.bind(this);

		this.componentDidMount = this.componentDidMount.bind(this);
		this.initializeApp = this.initializeApp.bind(this);
		this.updateStickers = this.updateStickers.bind(this);
		this.updateStickersInfo = this.updateStickersInfo.bind(this);
		this.updateUser = this.updateUser.bind(this);
		this.back = this.back.bind(this);
		this.go = this.go.bind(this);
	}

	componentDidMount () {
		window.addEventListener('popstate', e => {
			e.preventDefault();
			this.back(e);
		});
		bridge.subscribe(async ({ detail: { type, data }}) => {
			console.log(type, data);
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			} else if (type === 'VKWebAppGetUserInfoResult'){
				if(!this.state.initialized){
					let login = await this.get('users.auth');
					if(login.access){
						/*
                        data.stickers = [ 0 ];
                        data.balance = 0;
                        data.stickers_history = [ { title: "Илай", preview: "https://vk.com/sticker/1-18781-128", added: false }, { title: "Бисквит", author: "Илья Ульянов", preview: "https://vk.com/sticker/2-533-cover-140b-0" } ];
                        */
						this.setState({ initialized: true, popout: null });
						this.initializeApp();
					}
				}
			} else if (type === 'VKWebAppCallAPIMethodFailed'){
				if(data.error_data.error_reason.error_code === 14){
					let c = this.state.captcha;
					c.done = null;
					c.url = data.error_data.error_reason.captcha_img;
					c.sid = data.error_data.error_reason.captcha_sid;
					console.log(c);
					this.setState({ captcha: c, popout: null });
					this.go('input_captcha');
					for(var i = 0; i < 2; i++){
					    if(this.state.captcha.done === null){
					        i--;
                            await this.sleep(1000);
                        }else{
					        break;
                        }
                    }

					this.setState({ popout: <ScreenSpinner/> });
					let request_params = data.error_data.error_reason.request_params;
					let params = {};
					let method = '';
					let request_id = '';
					for(var i = 0; i < request_params.length; i++){
						let key = request_params[i].key;
						let value = request_params[i].value;
						if(key !== 'api_id' && key !== 'format' && key !== 'oauth'){
							if(key === 'method'){
								method = value;
							}else if (key === 'request_id'){
								request_id = value;
							}else{
								params[key] = value;
							}
						}
					}
					params.access_token = access_token;
					params.captcha_sid = this.state.captcha.sid;
					params.captcha_key = this.state.captcha.text;
					try{
						let res_back = await bridge.sendPromise("VKWebAppCallAPIMethod", {
							"method": method,
							"request_id": request_id,
							"params": params
						});
						this.setState({ captcha : { text: '', url: '', done: false } });
						this.back();
					}catch (e) {

					}
				}
			}
		});
		this.initializeApp();
	}

	initializeApp() {
		this.updateStickers();
		this.updateUser();
	}
	async updateStickers() {
		let response = await this.get('stickers.get');
		this.setState({stickers: response.stickers});
		let stickers_panel_new = [];
		let stickers = this.state.stickers;
		for (let i = 0; i < stickers.length; i++) {
			let stickers_ = [];
			for (let j = 0; j < stickers[i].stickers.length; j++) {
				stickers_.push(
					<StickerImg mode={"small"} src={stickers[i].stickers[j]}/>
				);
			}

			stickers[i].stickers = stickers_;
			stickers[i].id = i;
			stickers_panel_new.push(
				<StickerButton
					title={stickers[i].title}
					author={stickers[i].author}
					src={stickers[i].preview}
					price={stickers[i].price}
					id={"sticker_" + i}
					mode={"cell"}
					onClick={async (e) => {
						let id = parseInt(e.currentTarget.id.split('_')[1]);
						this.setState({sticker_info: stickers[id], popout: <ScreenSpinner/>});
						let response = await this.get('users.get');
						let user_ = await bridge.sendPromise('VKWebAppGetUserInfo');
						this.setState({user: {...user_, ...response.user}, popout: null});
						this.go("sticker_info");
					}}
				/>
			);
		}
		stickers_panel_new = stickers_panel_new.reverse();
		let stickers_panel_popular = stickers_panel_new.sort(function (itemA, itemB) {
			return (itemA.sales === 0 ? 0 : itemA.sales.length) - (itemB.sales === 0 ? 0 : itemB.sales.length);
		}).reverse();
		this.setState({stickers_panel_new, stickers_panel_popular});
	}
	async updateStickersInfo() {
		await this.updateStickers();
		await this.updateUser();
		this.setState({ sticker_info: this.state.stickers[this.state.sticker_info.id] });
	}
	async updateUser() {
		let response = await this.get('users.get');
		let user_ = await bridge.sendPromise('VKWebAppGetUserInfo');
		this.setState({user: {...user_, ...response.user}});
		let user_stickers = [];
		for (let i = 0; i < this.state.user.stickers.length; i++) {
			let sticker = this.state.stickers[i];
			user_stickers.push(
				<StickerButton
					src={sticker.preview}
					id={"user_sticker_" + i}
					mode={"img"}
					onClick={(e) => {
						let id = parseInt(e.currentTarget.id.split('_')[2]);
						this.setState({activeStory: "tab_main", sticker_info: this.state.stickers[id]});
						setTimeout(() => {
							this.go("sticker_info");
							}, 50)
					}}
				/>
			);
		}
		this.setState({user_stickers});

		let user_stickers_history = [];
		for (let i = 0; i < this.state.user.stickers_history.length; i++) {
			let sticker = this.state.user.stickers_history[i];
			user_stickers_history.push(
				<StickerButton
					title={sticker.title}
					src={sticker.preview}
					mode2={sticker.hasOwnProperty('added') ? (sticker.added ? 'commerce' : 'destructive') : 'primary'}
					button={sticker.hasOwnProperty('added') ? (sticker.added ? 'Одобрено' : 'Отклонено') : 'На проверке'}
					mode={"cell"}
				/>
			);
		}
		this.setState({user_stickers_history});
	}
	sleep(ms) {
		return new Promise(resolve => setTimeout(resolve, ms));
	}
	back = () => {
		let activeStory = this.state.activeStory;
		let activePanel = activeStory === 'tab_main' ? this.state.activePanelShop : activeStory === 'tab_other' ? this.state.activePanelOther : '';
		let panel_to_change = activeStory === 'tab_main' ? 'activePanelShop' : activeStory === 'tab_other' ? 'activePanelOther' : '';
		if((activeStory === 'tab_main' && activePanel==='main') || (activeStory === 'tab_other' && activePanel==='profile')){
			bridge.send("VKWebAppClose", {"status": "success"});
		}else {
			let history = activeStory === 'tab_main' ? this.state.historyShop : activeStory === 'tab_other' ? this.state.historyOther : '';
			if(history.length === 1) {
				bridge.send("VKWebAppClose", {"status": "success"});
			} else if (history.length > 1) {
				history.pop();
				let p = history[history.length - 1];
				this.setState({[panel_to_change]: p});
			}
		}
	};
	go(panel) {
		let activeStory = this.state.activeStory;
		let panel_to_change = activeStory === 'tab_main' ? 'activePanelShop' : activeStory === 'tab_other' ? 'activePanelOther' : '';
		let history = activeStory === 'tab_main' ? this.state.historyShop : activeStory === 'tab_other' ? this.state.historyOther : '';
		if(history[history.length-1]!==panel) {
			history.push(panel);
			let history_to_change = activeStory === 'tab_main' ? 'historyShop' : activeStory === 'tab_other' ? 'historyOther' : '';
			window.history.pushState({panel: panel}, 'Title');
			this.setState({[panel_to_change]: panel, [history_to_change]: history});
		}
	}

	onStoryChange (e) {
		let story = e.currentTarget.dataset.story;
		if(this.state.activeStory===story){
			if(story==='tab_main' && this.state.activePanelShop !== 'main'){
				this.back();
			}else if(story==='tab_other' && this.state.activePanelOther !== 'profile'){
				this.back();
			}
		}
		this.setState({ activeStory: story });
	}

	onChange(e) {
		const { name, value } = e.currentTarget;
		let create_panel = this.state.create_panel;
		create_panel[name] = value;
		this.setState({ create_panel: create_panel });
	}

	uploadFile(element, evt) {
		let tgt = evt.target || window.event.srcElement,
			files = tgt.files;
		const t = this;
		if (FileReader && files && files.length) {
			let fr = new FileReader();
			fr.onload = function () {
				let res = fr.result; //base64 result, to get src -> array[0].props.src
				let res1 = t.state.create_panel;
				if(files[files.length-1].type.includes('image')){
					if(element==='upload_sticker_img'){
						let res10 = res1.hasOwnProperty('uploaded_stickers_img') ? res1.uploaded_stickers_img : [];
						res10.push(
						    <StickerImg
								mode={'small'}
                                id={'uploaded_sticker_'+res10.length}
                                src={res}
								onClick={(e)=>{ let id = parseInt(e.currentTarget.id.split('_')[2]); let res01 = t.state.create_panel; delete res01.uploaded_stickers_img[id];  t.setState({ create_panel: res01 }); }}
                            />
						);
						let stickers_count = res10.filter(function(val){ return val !== undefined; }).length;
						if(stickers_count<100){
							res1.uploaded_stickers_img = res10;
							t.setState({ create_panel: res1 });
						}
					}else{
						res1[element] = res;
						t.setState({ create_panel: res1});
					}
				}else{
					res1[element] = null;
					t.setState({ create_panel: res1});
				}
			}
			fr.readAsDataURL(files[0]);
		}

		// Not supported
		else {
			// fallback -- perhaps submit the input to an iframe and temporarily store
			// them on the server until the user's session ends.
		}
	}

	toggleCreateStickerContext () {
		this.setState({ createStickerContextOpened: !this.state.createStickerContextOpened });
	}

	urlToBase64(url){
		return new Promise(resolve => {
			let img = new Image();
			img.crossOrigin = 'Anonymous';
			img.onload = function(){
				let canvas = document.createElement('CANVAS'),
					ctx = canvas.getContext('2d'), dataURL;
				canvas.height = img.height;
				canvas.width = img.width;
				ctx.drawImage(img, 0, 0);
				dataURL = canvas.toDataURL("image/png");
				canvas = null;
				resolve(dataURL);
			};
			img.src = url;
		});
	}

	async base64ToMultipart(base64) {
		return new Promise(resolve => {
			fetch(base64)
				.then(res => res.blob())
				.then(blob => {
					const fd = new FormData();
					const file = new File([blob], "sticker.png");
					fd.append('file', file);
					resolve(fd);
				});
		});
	}

	async sync(stickers_id) {
		this.setState({ popout: <ScreenSpinner/> });
		try{
			access_token = await bridge.sendPromise("VKWebAppGetAuthToken", {"app_id": 7363654, "scope": "docs"});
			access_token = access_token.access_token;
			upload_url1 = await bridge.sendPromise("VKWebAppCallAPIMethod", {"method": "docs.getUploadServer", "request_id": "upload_server", "params": {"group_id":"193090865", "v":"5.00", "access_token":access_token}});
			upload_url1 = upload_url1.response.upload_url;
			let stickers = this.state.stickers[stickers_id].stickers;
			for(let i = 0; i < stickers.length; i++){
				let sticker_url = stickers[i].props.src;
				await this.urlToBase64(sticker_url).then(async base64Img => {
					await this.base64ToMultipart(base64Img).then(async multipart => {
						await fetch('https://cors-anywhere.herokuapp.com/'+upload_url1, {method: 'POST', body: multipart})
							.then(res => res.json())
							.then(async res => {
								if(this.state.captcha.done === false){
									try{
										let file = res.file;
										let save = await bridge.sendPromise("VKWebAppCallAPIMethod", {
											"method": "docs.save",
											"request_id": "save_graffiti",
											"params": {"file":file, "v": "5.103", "access_token": access_token}
										});

										await this.get('stickers.sync', { id_: i, id: stickers_id, owner_id: save.response.graffiti.owner_id, doc_id: save.response.graffiti.id, access_key: save.response.graffiti.access_key });

										if(this.state.stickers[stickers_id].hasOwnProperty('data')){
											if(this.state.stickers[stickers_id].data.hasOwnProperty(i)){
												try{
													let remove = await bridge.sendPromise("VKWebAppCallAPIMethod", {
														"method": "docs.delete",
														"request_id": "del_doc",
														"params": {"owner_id": this.state.stickers[stickers_id].data[i].owner_id, "doc_id": this.state.stickers[stickers_id].data[i].doc_id, "v": "5.103", "access_token": access_token}
													});
												}catch (e) {}
											}
										}
									}catch (e) {
										console.log('error uploading...');
										i--;
									}
								}else if(this.state.captcha.done === null){
									i--;
								}
								await this.sleep(1000);
							});
					});
				});
			}
			this.showSnackBar('Стикеры синхронизированы', <Icon16Done fill="#fff" width={14} height={14} />, blueBackground);
		}catch (e) {
			this.showSnackBar('Произошла ошибка при получении данных', <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
		}
		this.updateStickersInfo();
		this.setState({ popout: null });
	}

	async get(method, params) {
		if(params === undefined || params === null) params = {};
		params.url = window.location.search.replace('?', '');
		let str = [];
		for (let p in params)
			if (params.hasOwnProperty(p)) {
				str.push(encodeURIComponent(p) + "=" + encodeURIComponent(params[p]));
			}
		params = str.join("&");
		let response = await fetch('https://ih1705413.vds.myihor.ru:4040/'+method+'?'+params, {method: 'GET'})
			.then(res => res.json())
			.then(res => {
				return res;
			});
		console.log(response);
		return response;
	}

	async install(stickers_id) {
		this.setState({ popout: <ScreenSpinner/> });
		try{
			access_token = await bridge.sendPromise("VKWebAppGetAuthToken", {"app_id": 7363654, "scope": "docs"});
			access_token = access_token.access_token;
			let stickers = await this.get('stickers.get');
			stickers = stickers.stickers[stickers_id];
			if(stickers.hasOwnProperty('data')){
				let data = stickers.data;
				for(var i = 0; i < data.length; i++){
					if(this.state.captcha.done === false){
						try{
							if(this.state.user.hasOwnProperty('stickers_cache')){
								if(this.state.user.stickers_cache.hasOwnProperty(stickers_id+'')){
									try{
										let remove = await bridge.sendPromise("VKWebAppCallAPIMethod", {
											"method": "docs.delete",
											"request_id": "del_doc",
											"params": {"owner_id": this.state.user.id, "doc_id": this.state.user.stickers_cache[stickers_id][i], "v": "5.103", "access_token": access_token}
										});
										await this.sleep(1000);
									}catch (e) {}
								}
							}
							let obj = data[i];
							let add = await bridge.sendPromise("VKWebAppCallAPIMethod", {
								"method": "docs.add",
								"request_id": "add_doc",
								"params": {"owner_id": obj.owner_id, "doc_id": obj.doc_id, "access_key": obj.access_key, "v": "5.103", "access_token": access_token}
							});
							let added = await this.get('stickers.add', { stickers_id: stickers_id, i: i, doc_id: add.response });
							if(added.access === false){
								break;
							}
						}catch (e) {
							console.log('error add...');
							i--;
						}
					}else if(this.state.captcha.done === null){
						i--;
					}
					await this.sleep(1000);
				}
				this.showSnackBar('Стикеры установлены', <Icon16Done fill="#fff" width={14} height={14} />, blueBackground);
			}else{
				this.showSnackBar('Стикеры не синхронизированы', <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
			}
		}catch (e) {
			this.showSnackBar('Произошла ошибка при получении данных', <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
		}
		this.updateStickersInfo();
		this.setState({ popout: null });
	}

	async delete (stickers_id) {
		this.setState({ popout: <ScreenSpinner/> });
		try{
			access_token = await bridge.sendPromise("VKWebAppGetAuthToken", {"app_id": 7363654, "scope": "docs"});
			access_token = access_token.access_token;
			if(this.state.user.hasOwnProperty('stickers_cache')) {
				if (this.state.user.stickers_cache.hasOwnProperty(stickers_id + '')) {
					for(var i = 0; i < this.state.user.stickers_cache[stickers_id].length; i++){
						if(this.state.captcha.done === false){
							try{
								let remove = await bridge.sendPromise("VKWebAppCallAPIMethod", {
									"method": "docs.delete",
									"request_id": "del_doc",
									"params": {"owner_id": this.state.user.id, "doc_id": this.state.user.stickers_cache[stickers_id][i], "v": "5.103", "access_token": access_token}
								});
							}catch (e) {
								console.log('error delete...');
								i--;
							}
						}else if(this.state.captcha.done === null){
							i--;
						}
						await this.sleep(1000);
					}
					await this.get('stickers.delete', { stickers_id: stickers_id });
					this.showSnackBar('Стикеры очищены', <Icon16Done fill="#fff" width={14} height={14} />, blueBackground);
				}
			}
		}catch (e) {
			this.showSnackBar('Произошла ошибка при получении данных', <Icon16Cancel fill="#fff" width={14} height={14} />, redBackground);
		}
		this.updateStickersInfo();
		this.setState({ popout: null });
	}

	showSnackBar (text, icon, style){
		this.setState({ snackbar: null });
		this.setState({ snackbar:
				<Snackbar
					layout="vertical"
					onClose={() => this.setState({ snackbar: null })}
					before={<Avatar size={24} style={style}>{icon}</Avatar>}
				>
					{text}
				</Snackbar>
		});
	}

	render() {
		return (
			<Epic activeStory={this.state.activeStory} tabbar={
				<Tabbar>
					<TabbarItem
						onClick={this.onStoryChange}
						selected={this.state.activeStory === 'tab_main'}
						data-story="tab_main"
					><Icon28MarketOutline/></TabbarItem>
					<TabbarItem
						onClick={this.onStoryChange}
						selected={this.state.activeStory === 'tab_other'}
						data-story="tab_other"
					><Icon28UserOutline /></TabbarItem>
				</Tabbar>
			}>
				<View id="tab_main" activePanel={this.state.activePanelShop} popout={this.state.popout} history={this.state.historyShop} onSwipeBack={this.back} header={false}>
					<Panel id="main" separator={false}>
						<PanelHeaderSimple
							separator={false}
						>
							Стикеры
						</PanelHeaderSimple>
						<Tabs>
							<TabsItem
								onClick={() => this.setState({ slideTabIndex: 0 })}
								selected={this.state.slideTabIndex === 0}
							>
								Новинки
							</TabsItem>
							<TabsItem
								onClick={() => this.setState({ slideTabIndex: 1 })}
								selected={this.state.slideTabIndex === 1}
							>
								Популярные
							</TabsItem>
						</Tabs>
						<Separator />
						<PullToRefresh onRefresh={async () => {
							this.setState({ refreshedStickers: true });
							await this.updateStickers();
							this.setState({ refreshedStickers: false });
						}} isFetching={this.state.refreshedStickers}>
							<Gallery
								slideWidth="100%"
								style={{ height: "100%", margin: "8px" }}
								slideIndex={this.state.slideTabIndex}
								onChange={(slideIndex) => { this.setState({slideTabIndex: slideIndex}); }}>
								<div>
									<List style={{ marginTop: "15px" }}>
										{this.state.stickers_panel_new}
									</List>
								</div>

								<div>
									<List style={{ marginTop: "15px" }}>
										{this.state.stickers_panel_popular}
									</List>
								</div>
							</Gallery>
						</PullToRefresh>
					</Panel>
					<Panel id="sticker_info">
						<PanelHeaderSimple><PanelHeaderContent>
							Информация
						</PanelHeaderContent></PanelHeaderSimple>
						<PullToRefresh onRefresh={async () => {
							this.setState({ refreshedStickersInfo: true });
							await this.updateStickersInfo();
							this.setState({ refreshedStickersInfo: false });
						}} isFetching={this.state.refreshedStickersInfo}>
							<Div>
								<Cell before={<StickerImg mode={'medium'} src={this.state.sticker_info.preview}/>} description={this.state.sticker_info.author}>{this.state.sticker_info.title}</Cell>
								<br/>
								<StickerDescription>{this.state.sticker_info.description}</StickerDescription>
								<div style={{ marginTop: '10px'}}>
									{this.state.user.stickers.indexOf(this.state.sticker_info.id)>=0 ?
										<div style={{ display: 'flex' }}>
											<Button onClick={()=>{ this.install(this.state.sticker_info.id); }} size={"xl"} stretched>{'Установить'}</Button>
											{this.state.user.hasOwnProperty('stickers_cache') ? this.state.user.stickers_cache.hasOwnProperty(this.state.sticker_info.id) ?
												<Button style={{ marginLeft: '8px' }} mode={"secondary"} onClick={()=>{ this.delete(this.state.sticker_info.id); }} size={"xl"} stretched>{'Очистить'}</Button> : null : null}
										</div>
										:
										<div style={{ display: 'flex' }}>
											<Button size={"xl"} stretched mode="commerce">{'Получить за '+this.state.sticker_info.price+' руб.'}</Button>
										</div>}
									{this.state.user.is_admin && <div style={{ display: 'flex', marginTop: '10px' }}><Button onClick={()=> { this.sync(this.state.sticker_info.id); }} size={"xl"} stretched>{'Синхронизировать'}</Button></div>}
								</div>
								<div className="centered" style={{ marginTop: "18px" }}>
									<StickerGrid mode={"small"}>
										{this.state.sticker_info.stickers}
									</StickerGrid>
								</div>
							</Div>

							<Div>
								<CardElements>
									<CardElement text={'Текст элемента 1'}/>
									<CardElement text={'Текст элемента 2'}/>
									<CardElement text={'Текст элемента 3'}/>
								</CardElements>
							</Div>
							{this.state.snackbar}
						</PullToRefresh>
					</Panel>
					<Panel id="input_captcha">
						<PanelHeaderSimple><PanelHeaderContent>
							Ввод капчи
						</PanelHeaderContent></PanelHeaderSimple>
						<FormLayout>
							<div className="centered">
								<img width={'60%'} src={this.state.captcha.url}/>
							</div>
							<Input
								align={'center'}
								value={this.state.captcha.text}
								onChange={(e)=> { let c = this.state.captcha; c.text = e.currentTarget.value; this.setState({ captcha: c }); }}
							/>
							<Button size="xl" onClick={()=> { let c = this.state.captcha; c.done = true; this.setState({ captcha: c });  }}>Отправить</Button>
						</FormLayout>
					</Panel>
				</View>
				<View id="tab_other" activePanel={this.state.activePanelOther} popout={this.state.popout} history={this.state.historyOther} onSwipeBack={this.back} header={false}>
					<Panel id="profile">
						<PanelHeaderSimple><PanelHeaderContent>
							Профиль
						</PanelHeaderContent></PanelHeaderSimple>
						<PullToRefresh onRefresh={async () => {
							this.setState({ refreshedProfile: true });
							await this.updateUser();
							this.setState({ refreshedProfile: false });
						}} isFetching={this.state.refreshedProfile}>
							<Div>
								<div className="centered">
									<Avatar src={this.state.user.photo_100} size={100}/>
								</div>
								<br/>
								<div className="centered" style={{ fontSize: "large"}}>
									{this.state.user.first_name+' '+this.state.user.last_name}
								</div>
								<br/>
								<Group separator="hide">
									<Cell asideContent={<Icon16Dropdown style={{ transform: `rotate(${this.state.balance_manipulation ? '180deg' : '0'})` }} />} onClick={()=> { this.setState({ balance_manipulation: !this.state.balance_manipulation }); }} indicator={this.state.user.balance+' ₽'}>Ваш баланс</Cell>
									{this.state.balance_manipulation && <div style={{ display: 'flex', justifyContent: 'flex-end', marginTop: '8px' }}>
										<Button size="l" before={<Icon16Down/>}>Пополнить</Button>
										<Button size="l" before={<Icon16Up/>} mode="secondary" style={{ marginLeft: 8 }}>Вывести</Button>
									</div>}
								</Group>
								<Group separator="hide" header={<Header mode="primary">Ваши стикеры</Header>}>
									<div className="centered" style={{ marginTop: "18px" }}>
										<StickerGrid mode={"big"}>
											{this.state.user_stickers}
											<StickerImg mode={'big'} onClick={()=> { this.go("create"); }}>
												+
											</StickerImg>
										</StickerGrid>
									</div>
								</Group>
							</Div>
						</PullToRefresh>
					</Panel>
					<Panel id="create">
						<PanelHeaderSimple>
							<PanelHeaderContent
								aside={<Icon16Dropdown style={{ transform: `rotate(${this.state.createStickerContextOpened ? '180deg' : '0'})` }} />}
								onClick={ () => { this.toggleCreateStickerContext(); }}
							>
								Добавить стикеры
							</PanelHeaderContent>
						</PanelHeaderSimple>
						<PanelHeaderContext opened={this.state.createStickerContextOpened} onClose={ () => { this.toggleCreateStickerContext(); }}>
							<List>
								<Cell
									before={<Icon28HistoryForwardOutline/>}
									onClick={ () => { this.toggleCreateStickerContext(); this.go("stickers_history") }}
								>
									История
								</Cell>
							</List>
						</PanelHeaderContext>
						<FormLayout>
							<Input
								type="text"
								top="Название стикер-пака"
								name="title"
								value={this.state.create_panel.title}
								onChange={this.onChange}
								status={this.state.create_panel.title ? this.state.create_panel.title.length <= 20 ? 'valid' : 'error' : 'error'}
								bottom={this.state.create_panel.title ? this.state.create_panel.title.length <= 20 ? '' : 'Максимальная длина - 20' : 'Пожалуйста, введите название'}
							/>
							<Input
								type="text"
								top="Описание"
								name="description"
								value={this.state.create_panel.description}
								onChange={this.onChange}
								status={this.state.create_panel.description ? this.state.create_panel.description.length <= 100 ? 'valid' : 'error' : 'error'}
								bottom={this.state.create_panel.description ? this.state.create_panel.description.length <= 100 ? '' : 'Максимальная длина - 100' : 'Пожалуйста, введите описание'}
							/>
							<Input
								type="number"
								top="Цена"
								name="price"
								value={this.state.create_panel.price}
								onChange={this.onChange}
								status={this.state.create_panel.price > 0 ? this.state.create_panel.price <= 1000 ? this.state.create_panel.price.toString().includes('.') ? 'error' : 'valid' : 'error' : 'error'}
								bottom={this.state.create_panel.price > 0 ? this.state.create_panel.price <= 1000 ? this.state.create_panel.price.toString().includes('.') ? 'Число должно быть целым' : '' : 'Максимальная цена- 1 000 руб.' : 'Минимальная цена - 1 руб.'}
							/>

							<StickerUploadButton
								src={this.state.create_panel.upload_preview_img && this.state.create_panel.upload_preview_img}
								title={'Превью'}
								description={'Загрузите главную фотографию стикер-пака (128×128px)'}
								onChange={(event)=> {this.uploadFile('upload_preview_img', event)}}
							/>

							<StickerUploadButton
								title={'Стикеры'}
								description={'Загрузите все стикеры (минимум - 4), которые будут содержаться в паке (128×128px)'}
								onChange={(event)=> {this.uploadFile('upload_sticker_img', event)}}
							/>

							<div className="centered" style={{ paddingBottom: "16px" }}>
								<div style={{ minWidth: "64px", maxWidth: "296px", display: "grid", gridTemplateColumns: "repeat(auto-fill, 64px)", gridGap: "10px" }}>
									{this.state.create_panel.uploaded_stickers_img}
								</div>
							</div>

							<Button size="xl" disabled={this.state.create_panel.title ? this.state.create_panel.description ? this.state.create_panel.price > 0 ? this.state.create_panel.title.length <= 20 ? this.state.create_panel.description.length <= 100 ? this.state.create_panel.price <= 1000 ? this.state.create_panel.price.toString().includes('.') ?  true : this.state.create_panel.upload_preview_img ? this.state.create_panel.uploaded_stickers_img ? this.state.create_panel.uploaded_stickers_img.filter(function(val){ return val !== undefined; }).length >= 4 ? false : true : true : true : true : true : true : true : true : true }>Продолжить</Button>
						</FormLayout>
					</Panel>
					<Panel id={"stickers_history"}>
						<PanelHeaderSimple><PanelHeaderContent>
							История
						</PanelHeaderContent></PanelHeaderSimple>
						{this.state.user_stickers_history && this.state.user_stickers_history.length > 0 ? <Div>
							<List>
								{this.state.user_stickers_history}
							</List>
						</Div>
						:
							<Placeholder
								icon={<Icon56HistoryOutline/>}
								header={'Пусто'}
								stretched
							>
								Вы пока еще не создавали стикеры
							</Placeholder>}
					</Panel>
				</View>
			</Epic>
		);
	}
}

export default App;